<template>
	<div>
		<router-link class="movie-section" :to="url">
			<img class="movie-img" :src="movie.img">
			<div class="movie-info">
				<div class="movie-name line-ellipsis">
					{{movie.nm}}
					<span :class="['version',movie.version]"></span>
				</div>
				<div class="movie-score line-ellipsis">
					<div v-if="movie.globalReleased">
						<span v-if="movie.sc">
							观众评
							<span class="grade">{{movie.sc}}</span>
						</span>
						<span v-else>暂无评分</span>
					</div>
					<div v-else>
						<span class="grade">{{movie.wish}}</span>人想看
					</div>
				</div>
				<div class="movie-star line-ellipsis">主演：{{movie.star}}</div>
				<div v-if="movie.rt">{{movie.rt}}上映</div>
				<div v-else class="movie-showInfo line-ellipsis">{{movie.showInfo}}</div>
			</div>
			<div class="buy-tickets">
				<div v-if="movie.showst===3" class="btn">购票</div>
				<div v-else-if="movie.showst===1" class="btn want-see">想看</div>
				<div v-else-if="movie.showst===4" class="btn pre-sale">预售</div>
			</div>
		</router-link>
	</div>
</template>
<script>
export default {
  name: "movieSection",
  props: {
    movie: {
      type: Object
    }
  },
  computed: {
    url() {
      return "/movie/movie-detail?movieId=" + this.movie.id;
    }
  }
};
</script>

<style lang="scss">
.movie-section {
  position: relative;
  display: flex;
  align-items: center;
  height: 228px;
  padding: 0 30px;
  &::after {
    position: absolute;
    content: "";
    height: 0;
    width: calc(100% - 208px);
    right: 30px;
    bottom: 0;
    border-bottom: 1px solid #f0f0f0;
  }
  .movie-img {
    width: 128px;
    height: 180px;
  }
  .movie-info {
    margin: 0 20px;
    font-size: 26px;
    color: #666;
    width: 60%;
    div {
      margin-bottom: 6px;
    }
  }
  .movie-name {
    font-size: 34px;
    color: #333;
    padding-right: 10px;
    .version {
      display: inline-block;
      height: 28px;
      background-size: contain;
      background-repeat: no-repeat;
      margin-left: 10px;
      margin-top: 10px;
    }
  }
}

.v3d.imax {
  width: 86px;
  background-image: url(data:image/png;base64,);
}

.v3d {
  width: 34px;
  background-image: url(data:image/png;base64,);
}

.v2d.imax {
  width: 86px;
  background-image: url(data:image/png;base64,);
}

.grade {
  font-weight: 700;
  color: #faaf00;
  font-size: 30px;
}
</style>
